import React from 'react';
import Weekday from './Weekday';
import '../Styles/Weekly.css';


function Weekly({states}) {

    let weekdays = states.weatherData.daily;
    let currentTime = states.currentTime.Datetime;
    let dayOfMonth = (currentTime[8]*10+currentTime[9]*1);
    console.log(dayOfMonth);
    let month = 'January';
    let nextWeek = Array.from(weekdays).filter((item,index)=>{
        return index > 0;
    });

    switch(currentTime[5]+currentTime[6]) {
        case '01':
            month = 'Jan'
            break;
        case '02':
            month = 'Feb'
            break;
        case '03':
            month = 'Mar'
            break;
        case '04':
            month = 'Apr'
            break;
        case '05':
            month = 'May'
            break;
        case '06':
            month = 'Jun'
            break;
        case '07':
            month = 'Jul'
            break;
        case '08':
            month = 'Aug'
            break;
        case '09':
            month = 'Sep'
            break;
        case '10':
            month = 'Oct'
            break;
        case '11':
            month = 'Nov'
            break;
        case '12':
            month = 'Dec'
            break;
        default: month = 'Jan'
    }

    return (
        <div className="weekly">
            {nextWeek.map((item,index)=>{
                dayOfMonth = dayOfMonth+1;
                let weatherCondition = item.weather[0].id;
                let dayOfWeek = 'Fail';
                let timeDifference = (item.dt - 1578297600)/86400;
                console.log(timeDifference);
                switch(timeDifference%7) {
                    case 0:
                        dayOfWeek = 'Mon'
                        break;
                    case 1:
                        dayOfWeek = 'Tue'
                        break;
                    case 2:
                        dayOfWeek = 'Wed'
                        break;
                    case 3:
                        dayOfWeek = 'Thu'
                        break;
                    case 4:
                        dayOfWeek = 'Fri'
                        break;
                    case 5:
                        dayOfWeek = 'Sat'
                        break;
                    case 6:
                        dayOfWeek = 'Sun'
                        break;
                    default: dayOfWeek = 'Mon'
                }
                return <Weekday key={index} day={dayOfMonth} month={month} dayOfWeek={dayOfWeek} 
                dayTemp={Math.round(item.feels_like.day)} nightTemp={Math.round(item.feels_like.night)} condition={weatherCondition}/>
            })}
        </div>
    );
}



export default Weekly;