import React from 'react';
import '../Styles/Weekday.css';
import '../WebFont/font-css/LineIcons.css';

function Weekday ({day,month,dayOfWeek,dayTemp,nightTemp,condition}) {


    //Choosing the weather name

    let weatherStateName = 'Sunny';

    if(condition>800){
        weatherStateName='Cloudy';
      }
      else if(condition>799){
        weatherStateName = 'Sunny';
      }
      else if(condition>700){
        weatherStateName = 'Foggy';
      }
      else if(condition>599){
        weatherStateName = 'Snowy';
      }
      else if(condition>299){
        weatherStateName = 'Rainy';
      }
      else {
        weatherStateName = 'Stormy';
      }
  
      //Choosing correct weather icon
  
      let weatherIcon = 'lni lni-sun';
  
      switch(weatherStateName) {
        case 'Sunny': weatherIcon = 'lni lni-sun';
        break;
        case 'Cloudy': weatherIcon = 'lni lni-cloud';
        break;
        case 'Rainy': weatherIcon = 'lni lni-rain';
        break;
        case 'Foggy': weatherIcon = 'lni lni-cloud';
        break;
        case 'Snowy': weatherIcon = 'lni lni-star-empty';
        break;
        case 'Stormy': weatherIcon = 'lni lni-thunder';
        break;
        default: weatherIcon = 'lni lni-sun';
      }
  

    return (
        <div className="weekday">
            <div className="date">
                <p className="month">{month}</p>
                <p className="day">{day}</p>
                <p className="day-name">{dayOfWeek}</p>
            </div>
            <i className={weatherIcon}></i>
            <div className="grid">
                <div className="grid-l">
                    <div className="item it1">
                        <p>D</p>
                    </div>
                    <div className="item">
                        <p>N</p>
                    </div>
                </div>
                <div className="grid-r">
                    <div className="item-r it1">
                        <p className="day-temp">{dayTemp} 째C</p>
                    </div>
                    <div className="item-r">
                        <p className="night-temp">{nightTemp} 째C</p>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Weekday;