import React from 'react';
import '../Styles/Mainblock.css';
import '../WebFont/font-css/LineIcons.css';


function Mainblock({states}) {

    let currentWeatherState = states.weatherData.current.weather[0].id;
    let weatherStateName = 'Sunny';

    //Choosing the weather name

    if(currentWeatherState>800){
      weatherStateName='Cloudy';
    }
    else if(currentWeatherState>799){
      weatherStateName = 'Sunny';
    }
    else if(currentWeatherState>700){
      weatherStateName = 'Foggy';
    }
    else if(currentWeatherState>599){
      weatherStateName = 'Snowy';
    }
    else if(currentWeatherState>299){
      weatherStateName = 'Rainy';
    }
    else {
      weatherStateName = 'Stormy';
    }

    //Choosing the weather image

    let weatherImagePlaceholder = require('../img/sunny.svg');

    switch(weatherStateName) {
      case 'Sunny': weatherImagePlaceholder = require('../img/sunny.svg');
      break;
      case 'Cloudy': weatherImagePlaceholder = require('../img/cloudy.svg');
      break;
      case 'Rainy': weatherImagePlaceholder = require('../img/rainy.svg');
      break;
      case 'Foggy': weatherImagePlaceholder = require('../img/foggy.svg');
      break;
      case 'Snowy': weatherImagePlaceholder = require('../img/snowy.svg');
      break;
      case 'Stormy': weatherImagePlaceholder = require('../img/stormy.svg');
      break;
      default: weatherImagePlaceholder = require('../img/sunny.svg');
    }

    //Choosing correct weather icon

    let weatherIcon = 'lni lni-sun';

    switch(weatherStateName) {
      case 'Sunny': weatherIcon = 'lni lni-sun';
      break;
      case 'Cloudy': weatherIcon = 'lni lni-cloud';
      break;
      case 'Rainy': weatherIcon = 'lni lni-rain';
      break;
      case 'Foggy': weatherIcon = 'lni lni-cloud';
      break;
      case 'Snowy': weatherIcon = 'lni lni-star-empty';
      break;
      case 'Stormy': weatherIcon = 'lni lni-thunder';
      break;
      default: weatherIcon = 'lni lni-sun';
    }

    let current = states.weatherData.current;
    console.log(weatherStateName+'_mb mainblock');
    return (
      <div className={`mainblock ${weatherStateName+'_mb'}`}>
        <div className="author">
          <p className={`author-name ${weatherStateName+'_c1'}`}>by Seymur Rahimov</p>
          <a href="https://www.instagram.com/noiseymur" target="blank"><i class={`lni lni-instagram-filled ${weatherStateName+'_c1'}`}></i></a>
          <a href="https://www.facebook.com/noiseymur" target="blank"><i class={`lni lni-facebook-original ${weatherStateName+'_c1'}`}></i></a>
        </div>
        <div className="top">
          <div className="lside">
            <img className={weatherStateName} src={weatherImagePlaceholder} alt="weather-icon"/>
          </div>
          <div className="rside">
            <p className="today-its">Today it's</p>
            <div className="headers">
              <h1 className={`main ${weatherStateName+'_c2'}`}>{weatherStateName}</h1>
              <h1 className={`main2 ${weatherStateName+'_c1'}`}>{weatherStateName}</h1>
            </div>
          </div>
        </div>
        <div className="bottom">
          <div className="main-info">
            <p>in</p>
            <div className="underlined">
              <p className="loc-name">{states.cityName}, <span>Azerbaijan</span>
              </p>
              <i className={weatherIcon}></i>
              <p className="current-deg">{Math.round(current.temp)} 째C</p>
            </div>
          </div>
          <div className="extra-data">
            <div className="default-names">
              <div className="name-and-line">
                <p>UV index:</p>
                <div className="dashed-line"></div>
              </div>
              <div className="name-and-line">
                <p>Humidity:</p>
                <div className="dashed-line"></div>
              </div>
              <div className="name-and-line">
                <p>Wind speed:</p>
                <div className="dashed-line"></div>
              </div>
              <div className="name-and-line">
                <p>Atmospheric pressure:</p>
                <div className="dashed-line"></div>
              </div>
            </div>
            <div className="variable-data">
              <p className="uv-index">{current.uvi}</p>
              <p className="humidity">{current.humidity}%</p>
              <p className="wind-speed">{current.wind_speed} m/s</p>
              <p className="atm-pressure">{current.pressure/10} kPa</p>
            </div>
          </div>
        </div>
      </div>
    );
}


export default Mainblock;